import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Redirect } from "react-router-dom";
import {
	selectCartItems,
	selectCartItemsTotal,
} from "./../redux/selectors/cart.js";
import successes from "./../redux/messages/success.json";
import errors from "./../redux/messages/errors.json";

import { add_coupon } from "./../redux/actions/products";
import { order } from "./../redux/actions/orders";
import { validateInfo } from "../components/Validate/validateOrder.jsx";
import isEmpty from "../components/Validate/isEmpty.jsx";
import { selectCoupon } from "../redux/selectors/products.js";
import { ADD_ERROR, ADD_MESSAGE, CLEAR_CART } from "../redux/types/types.js";
import Breadcumb from "../components/Products/Breadcumb.jsx";
import BigHeader from "../components/Products/BigHeader.jsx";
import { useTranslation } from "react-i18next";

const mapState = (state) => ({
	totalAmount: selectCartItemsTotal(state),
	isAuthenticated: state.auth.isAuthenticated,
	user: state.auth.user,
	cartItems: selectCartItems(state),
	coupon: selectCoupon(state),
});

function Order() {
	const { t } = useTranslation(["order", "common", "cities", "form"]);
	const [total, setTotal] = useState(0);
	const [couponInput, setCouponInput] = useState("");
	const dispatch = useDispatch();
	const { totalAmount, isAuthenticated, user, cartItems, coupon } = useSelector(
		mapState
	);

	const [dostawka, setDostawka] = useState(15);

	const [isSubmitting, setSubmitting] = useState(false);
	const [redirect, setRedirect] = useState(false);

	const [formInputs, setFormInputs] = useState({
		fullName: user?.username,
		phoneNumber: user?.phone_number,
		email: user?.email,
		city: user?.city || "Ag",
		address: user?.address,
		notes: "👉",
	});
	const [formErrors, setFormErrors] = useState(null);

	const handleChange = (e) => {
		const value = e.target.value;
		const name = e.target.name;
		setFormInputs({ ...formInputs, [name]: value });
	};

	const handleSubmit = () => {
		if (cartItems.length < 1) {
			dispatch({ type: ADD_ERROR, payload: errors.error6 });
			return setRedirect(true);
		}
		setFormErrors(validateInfo(formInputs));
		if (isEmpty(formErrors)) {
			setSubmitting(true);
			dispatch(order(formInputs, cartItems, coupon)).then((result) => {
				setSubmitting(false);

				if (result?.status === 201) {
					dispatch({ type: CLEAR_CART });
					dispatch({ type: ADD_MESSAGE, payload: successes.success8 });
					setRedirect(true);
				} else {
					return dispatch({ type: ADD_ERROR, payload: errors.error3 });
				}
			});
		} else {
			dispatch({ type: ADD_ERROR, payload: errors.error7 });
		}
	};

	useEffect(() => {
		setFormErrors(validateInfo(formInputs));
	}, [formInputs]);

	const handleAddCoupon = () => {
		if (couponInput?.length >= 1) {
			dispatch(add_coupon(couponInput)).then((result) => {
				if (result === "Request failed with status code 404") {
					dispatch({ type: ADD_ERROR, payload: errors.error8 });
				} else if (result?.status === 200) {
					setCouponInput("");
					dispatch({ type: ADD_MESSAGE, payload: successes.success9 });
				} else {
					dispatch({ type: ADD_ERROR, payload: errors.error3 });
				}
			});
		} else {
			dispatch({ type: ADD_ERROR, payload: errors.error9 });
		}
	};

	useEffect(() => {
		// Egerde Alyan Haryt 500 manatdan yokarda bolsa onda dostawka  mugt
		if (totalAmount > 500) {
			setDostawka(0);
		} else {
			setDostawka(15);
		}
		// Egerde kupony bolmasa
		if (!coupon) {
			// Alyan haryty bar bolsa
			if (totalAmount > 0) {
				return setTotal(totalAmount + dostawka);
			} else {
				return setTotal(0);
			}
		} else {
			const disAmount = ((totalAmount + dostawka) * coupon.discount) / 100;
			// Kupony bolsa
			if (totalAmount > 0) {
				return setTotal(totalAmount - disAmount);
			}
			return setTotal(0);
		}
	}, [coupon, totalAmount, dostawka]);

	// if (!isAuthenticated) return <Redirect to="/login" />;
	if (redirect) return <Redirect to="/" />;

	return (
		<>
			<BigHeader searchedCategoryName={t("order:Order")} />

			<div className="container ">
				<Breadcumb searchedCategoryName={t("order:Order")} topmar={true} />

				<div className="row py-5">
					{/* Order Form Start */}
					<div className="col-12 col-md-6 col-lg-8 ">
						<form className="row">
							<div className="mb-2 col-6 form-floating">
								<input
									type="text"
									class={`form-control ${
										formErrors?.fullName ? "is-invalid" : "is-valid"
									}`}
									id="nameInput"
									name="fullName"
									value={formInputs.fullName}
									autoFocus={true}
									onChange={(e) => handleChange(e)}
									placeholder="Full Name"
									style={{ borderRadius: "20px" }}
								/>
								<label for="nameInput" style={{ left: "10px" }}>
									{t("form:Full_name")} <span className="text-danger">*</span>
								</label>
								<div class="invalid-feedback">{formErrors?.fullName}</div>
							</div>
							<div className="mb-2 col-6 form-floating">
								<input
									type="number"
									class={`form-control ${
										formErrors?.phoneNumber ? "is-invalid" : "is-valid"
									}`}
									id="phoneInput"
									name="phoneNumber"
									value={formInputs.phoneNumber}
									onChange={(e) => handleChange(e)}
									placeholder="Phone Number"
									style={{ borderRadius: "20px" }}
								/>
								<label for="phoneInput" style={{ left: "10px" }}>
									{t("form:Phone_number")}{" "}
									<span className="text-danger">*</span>
								</label>
								<div class="invalid-feedback">{formErrors?.phoneNumber}</div>
							</div>
							<div className="mb-2 col-6 form-floating">
								<input
									type="email"
									class={`form-control ${
										formErrors?.email ? "is-invalid" : "is-valid"
									}`}
									id="emailInput"
									name="email"
									value={formInputs.email}
									onChange={(e) => handleChange(e)}
									placeholder="Email"
									style={{ borderRadius: "20px" }}
								/>
								<label for="emailInput" style={{ left: "10px" }}>
									{t("form:Email")}
								</label>
								<div class="invalid-feedback">{formErrors?.email}</div>
							</div>
							<div className="mb-2 col-6 form-floating">
								<select
									style={{ borderRadius: "20px" }}
									value={formInputs.city}
									onChange={(e) => handleChange(e)}
									class="form-select"
									id="CityInput"
									name="city"
									aria-label="Floating label select example"
								>
									<option value="Ag">{t("cities:Ashgabat")}</option>
									<option value="Mr">{t("cities:Mary")}</option>
									<option value="Ah">{t("cities:Ahal")}</option>
									<option value="Dz">{t("cities:Dashoguz")}</option>
									<option value="Bl">{t("cities:Balkan")}</option>
									<option value="Lb">{t("cities:Lebap")}</option>
								</select>
								<label for="CityInput" style={{ left: "10px" }}>
									{t("form:Choose_city")}
								</label>
							</div>
							<div className="mb-2 col-12 form-floating">
								<textarea
									class={`form-control ${
										formErrors?.address ? "is-invalid" : "is-valid"
									}`}
									placeholder="Address"
									value={formInputs.address}
									onChange={(e) => handleChange(e)}
									id="floatingTextarea"
									name="address"
									style={{ borderRadius: "20px", height: "65px" }}
								></textarea>
								<label for="floatingTextarea" style={{ left: "10px" }}>
									{t("form:Address")} <span className="text-danger">*</span>
								</label>
								<div class="invalid-feedback">{formErrors?.address}</div>
							</div>
							<div className="mb-2 col-12 form-floating">
								<textarea
									class={`form-control ${
										formErrors?.notes ? "is-invalid" : "is-valid"
									}`}
									placeholder="Notes"
									id="floatingNotes"
									name="notes"
									value={formInputs.notes}
									onChange={(e) => handleChange(e)}
									style={{ borderRadius: "20px", height: "65px" }}
									required={true}
								></textarea>
								<label for="floatingNotes" style={{ left: "10px" }}>
									{t("form:Notes")} <span className="text-danger">*</span>
								</label>
								<div class="invalid-feedback">{formErrors?.notes}</div>
							</div>
						</form>
						<h5 className="mt-4 ps-2">{t("order:delivery_&_pay")}</h5>
						{/* Eltip bermek we töleg */}
						<ul style={{ listStyleType: "disc" }}>
							<li>{t("order:delivery_turkm")}</li>

							<li>{t("order:operator_call_you")}</li>

							<li>{t("order:order_delivery")}</li>
						</ul>
					</div>
					{/* Order Form End */}

					{/* Subtotal Start */}
					<div className="col-12 col-md-6 col-lg-4">
						<div
							className=" bg-white position-relative my-3 mx-auto w-95 font-weight-bolder d-flex flex-column"
							style={{
								borderRadius: "30px",
								padding: "2rem",
								boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
							}}
						>
							<h3>{t("order:order_summary")}</h3>

							<p className="text-muted">{t("order:total_info")}</p>
							<div className="d-flex align-items-center justify-content-between my-2">
								<span>{t("order:Subtotal")}</span>
								<span style={{ fontWeight: 500 }}>
									{totalAmount.toFixed(2)} {t("common:manat")}
								</span>
							</div>
							{dostawka > 0 ? (
								<div className="d-flex align-items-center justify-content-between">
									<em>{t("order:delivery")}</em>
									<em>
										{dostawka.toFixed(2)} {t("common:manat")}
									</em>
								</div>
							) : (
								<div className="d-flex align-items-center justify-content-between">
									<del>
										<em>{t("order:delivery")}</em>
									</del>

									<em>{t("order:free")}</em>
								</div>
							)}
							{coupon && (
								<div className="d-flex text-danger my-1 align-items-center justify-content-between">
									<em>
										{t("form:Coupon")} ({coupon.code})
									</em>
									<em>-{coupon.discount} %</em>
								</div>
							)}
							{isAuthenticated && (
								<div className="input-group mt-3 position-relative">
									<input
										type="text"
										class="form-control"
										style={{ borderRadius: "15px" }}
										onKeyDown={(e) => {
											if (e.key === "Enter") {
												handleAddCoupon();
											}
										}}
										placeholder={t("order:have_a_coupon")}
										aria-label="Username"
										value={couponInput}
										onChange={(e) => setCouponInput(e.target.value)}
										aria-describedby="basic-addon1"
									/>
								</div>
							)}
							{couponInput && (
								<div
									role="button"
									onClick={() => handleAddCoupon()}
									className="btn  mt-3 ms-2 btn-danger "
									style={{
										width: "100%",
										borderRadius: "20px",
									}}
								>
									{t("order:apply_coupon")}
								</div>
							)}
						</div>

						<div
							style={{ fontWeight: 600 }}
							className="d-flex align-items-center justify-content-between font-weight-bold h5 my-2"
						>
							<div className="">{t("order:total")}</div>
							<div>
								{total.toFixed(2)} {t("common:manat")}
							</div>
						</div>

						<button
							className="btn  mt-3 ms-2 btn-primary"
							onClick={() => handleSubmit()}
							disabled={!isEmpty(formErrors) || cartItems.length < 1}
							style={{
								width: "100%",
								borderRadius: "20px",
								boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
							}}
						>
							{isSubmitting ? (
								<div class="spinner-border text-white" role="status">
									<span class="visually-hidden">Loading...</span>
								</div>
							) : (
								t("order:order")
							)}
						</button>
					</div>
				</div>
				{/* Subtotal End */}
			</div>
		</>
	);
}

export default Order;
